/* eslint-disable @typescript-eslint/ban-ts-comment */
import { debounce } from 'lodash-es';
import { Controller, Block } from '@ali/4ever-cangjie';
import { HoverBlock } from '@ali/4ever-utils';
import { updateDragHander } from '../actions';

const mouseMoveHandler = debounce(
  (
    event: MouseEvent,
    controller: Controller,
    zoomContainer: HTMLElement | null,
    dispatch: React.Dispatch<any>,
    zoom?: number,
  ) => {
    const { block } = HoverBlock.getBlock(event, controller.value.document, {
      zoomContainer,
      zoom,
      filterColumn: true,
    });
    if (Block.isBlock(block)) {
      dispatch(
        updateDragHander({
          viewKey: block.key,
        }),
      );
    } else {
      dispatch(updateDragHander());
    }
  },
  50,
);

export default mouseMoveHandler;
